.pagination {
    text-align: center;

    &__list {
        display: inline-flex;
        margin-inline: auto;
        column-gap: 25px;
    }

    &__item {
        width: 15px;
        aspect-ratio: 1;
        display: flex;

        
    }
    
    &__button {
        width: 100%;
        aspect-ratio: 1;
        border-radius: 50%;
        background-color: var(--border-color-gray);

    }
}